.footer-container {
    background-color:#33333396;
    display: flex;
}
.footer-wrapper {
    display: flex;
    justify-content: space-between;
    padding-bottom: 1rem;
    margin-bottom: 6%;
}
.foot-left{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: left;
    width: 40%;
    margin-left: 30px;
}
.foot-title{
    margin-top: 20px;
    margin-bottom: 20px;
    font-family: 'Fjalla One', sans-serif;
    font-size: 50px;
}
.exit-statement, .exit-statement-mobile {
    font-family: 'Dosis', sans-serif;
    font-size: 30px;
    text-align: center;
    line-height: 1.5;
}
.exit-statement-mobile{
    display: none;
}
.last-words-footer{
    font-weight: 500;
    font-family: 'Permanent Marker', cursive;
    font-size: 30px;
    text-align: center;
    line-height: 2.5;
}
.contact-foot{
    display: flex;
    justify-content: space-between;
    width: 50%;
}
.phone-foot{
    display: flex;
    flex-direction: column;
    justify-content: top;
    align-items: center;
    margin-right: 10%;
    margin-top: 5%;
}
.email-foot{
    display: flex;
    flex-direction: column;
    justify-content: top;
    align-items: center;
    margin-top: 5%;
}
.want-email, .want-phone{
    font-size: 35px;
    margin-bottom: 50px;
    font-family: 'Fjalla One', sans-serif;
}
.email-button, .phone-button {
    display: inline-block;
    padding: 15px 30px; /* Increased padding for a larger button */
    background-color: #666666;
    color: white;
    text-decoration: none;
    font-family: Arial, sans-serif;
    font-size: 28px; /* Adjusted font size */
    border-radius: 8px;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s;
}

.email-button:hover, .phone-button:hover {
    background-color: #cccccc;
}


.kokachin {
    width: 6rem;
}
.footer-container{
    display: flex;
    flex-direction: column;
    margin-top: 1%;
}
.bottom-footer-wrapper-mobile{
    display: none;
}
.bottom-footer-wrapper{
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;
}
.bottom-footer-container {
    margin-left: 15%;
}

.my-row {
    font-size: 13.5px;
    color: rgb(149, 149, 149);
    margin-bottom: 9px;
    text-align: center;
}


.year-container {
    margin-top: 15px;
    font-size: 14px;
    margin-bottom: 10px;
    margin-right: 5%;
    display: flex;
    align-content: baseline;
    align-items: baseline;
    flex-wrap: wrap-reverse;
}

.host  {
    color: #3f3f3f;
}
.git, .linked-in{
    width: 3rem;
    display: inline-block;
    background-color: #666666;
    color: white;
    text-decoration: none;
    border-radius: 8px;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s;

}
.git:hover, .linked-in:hover {
    background-color: #cccccc;
}

.c-w-m-header {
    text-align: center;
}

.connect-with-me-wrapper{
    display: flex;
    flex-direction: column;
    align-items: center;
}